import React, { useState, useEffect } from 'react';
import logo from './logo.svg';
import './App.css';

function App() {

  const [data, setData] = useState(null);
  const [stato, setStato] = useState(null);

  useEffect(() => {
    fetch("https://indicatori-api.herokuapp.com/v2/current/IT")
      .then(res => res.json())
      .then((result) => {
        setData(result.data);
        setStato(result.stato);
      }, (error) => {
        console.log(error);
      });
  }, []);



  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload 1.1.
        </p>
        <p>Data : {data}</p>
        <p>Stato : {stato}</p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
